import { Slot } from "@radix-ui/react-slot";
import clsx from "clsx";
import { ReactNode } from "react";

export interface ITextProps {
  size?: 'sm' | 'md' | 'lg';
  className?: string;
  children: ReactNode;
  asChild?: boolean;
}

export function Text({ size = 'md', className, children, asChild }: ITextProps) {
  const Comp = asChild ? Slot : 'span';

  return (
    <Comp
      className={
        clsx(
          "text-gray-100 font-sans",
          {
            "text-xs": size === 'sm',
            'text-sm': size === 'md',
            "text-md": size === 'lg',
          },
          className
        )
      }
    >
      {children}
    </Comp>
  );
}